.RollDice {
  display: flex;
  flex-flow: column;
}

.RollDice-container {
  display: flex;
  justify-content: center;
}

.RollDice-container > * {
  margin: 40px;
}

.RollDice button {
  align-self: center;
  font-size: 15px;
  background-color: black;
  color: white;
  border-radius: 5px;
  width: 165px;
  height: 45px;
}

.RollDice button:hover {
  background-color: rebeccapurple;
  cursor: pointer;
}

.RollDice-disable {
  cursor: not-allowed !important;
}

.RollDice-shake {
  animation: shake 1s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  perspective: 1000px;
}

@keyframes shake {
  10%,
  90% {
    transform: translate3d(-500px, 250px, -0);
  }
  20%,
  80% {
    transform: translate3d(-0, 250px, 0);
  }
  30%,
  50%,
  70% {
    transform: translate3d(500px, 0, -0);
  }
  40%,
  60% {
    transform: translate3d(250px, 500px, 0);
  }
}

